.login {
  background: #eceeee;
  border: 1px solid #42464b;
  border-radius: 6px;
  height: 257px;
  margin: 15% auto 0;
  width: 298px;
}

.signUp {
  background: #eceeee;
  border: 1px solid #42464b;
  border-radius: 6px;
  height: 20rem;
  margin: 15% auto 0;
  width: 298px;
}

.login h1 {
  background-image: -webkit-gradient(linear, left top, left bottom, from(top), color-stop(#f1f3f3), to(#d4dae0));
  background-image: -webkit-linear-gradient(top, #f1f3f3, #d4dae0);
  background-image: -o-linear-gradient(top, #f1f3f3, #d4dae0);
  background-image: linear-gradient(top, #f1f3f3, #d4dae0);
  border-bottom: 1px solid #a6abaf;
  border-radius: 6px 6px 0 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  color: #727678;
  display: block;
  height: 43px;
  font: 600 14px/1 'Open Sans', sans-serif;
  padding-top: 14px;
  margin: 0;
  text-align: center;
  text-shadow: 0 -1px 0 rgba(0,0,0,0.2), 0 1px 0 #fff;
}
input[type="password"], input[type="text"] {
  background: url('http://i.minus.com/ibhqW9Buanohx2.png') center left no-repeat, -webkit-gradient(linear, left top, left bottom, from(#d6d7d7), to(#dee0e0));
  background: url('http://i.minus.com/ibhqW9Buanohx2.png') center left no-repeat, -webkit-linear-gradient(top, #d6d7d7, #dee0e0);
  background: url('http://i.minus.com/ibhqW9Buanohx2.png') center left no-repeat, -o-linear-gradient(top, #d6d7d7, #dee0e0);
  background: url('http://i.minus.com/ibhqW9Buanohx2.png') center left no-repeat, linear-gradient(top, #d6d7d7, #dee0e0);
  border: 1px solid #a1a3a3;
  border-radius: 4px;
  -webkit-box-shadow: 0 1px #fff;
          box-shadow: 0 1px #fff;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  color: #696969;
  height: 39px;
  margin: 31px 0 0 29px;
  padding-left: 37px;
  -webkit-transition: -webkit-box-shadow 0.3s;
  transition: -webkit-box-shadow 0.3s;
  -o-transition: box-shadow 0.3s;
  transition: box-shadow 0.3s;
  transition: box-shadow 0.3s, -webkit-box-shadow 0.3s;
  width: 240px;
}
input[type="password"]:focus, input[type="text"]:focus {
  -webkit-box-shadow: 0 0 4px 1px rgba(55, 166, 155, 0.3);
          box-shadow: 0 0 4px 1px rgba(55, 166, 155, 0.3);
  outline: 0;
}
.show-password {
  display: block;
  height: 16px;
  margin: 26px 0 0 28px;
  width: 87px;
}
input[type="checkbox"] {
  cursor: pointer;
  height: 16px;
  opacity: 0;
  position: relative;
  width: 64px;
}
input[type="checkbox"]:checked {
  left: 29px;
  width: 58px;
}
.toggle {
  display: block;
  height: 16px;
  margin-top: -20px;
  width: 87px;
  z-index: -1;
}
input[type="checkbox"]:checked + .toggle { background-position: 0 -16px }
.forgot {
  color: #7f7f7f;
  display: inline-block;
  float: right;
  font: 12px/1 sans-serif;
  left: -19px;
  position: relative;
  text-decoration: none;
  top: 5px;
  -webkit-transition: color .4s;
  -o-transition: color .4s;
  transition: color .4s;
}
.forgot:hover { color: #3b3b3b }
input[type="submit"] {
  width:240px;
  height:35px;
  display:block;
  font-family:Arial, "Helvetica", sans-serif;
  font-size:16px;
  font-weight:bold;
  color:#fff;
  text-decoration:none;
  text-transform:uppercase;
  text-align:center;
  text-shadow:1px 1px 0px #37a69b;
  padding-top:6px;
  margin: 29px 0 0 29px;
  position:relative;
  cursor:pointer;
  border: none;
  background-color: #37a69b;
  background-image: -webkit-gradient(linear,left top, left bottom,from(top),color-stop(#3db0a6),to(#3111));
  background-image: -webkit-linear-gradient(top,#3db0a6,#3111);
  background-image: -o-linear-gradient(top,#3db0a6,#3111);
  background-image: linear-gradient(top,#3db0a6,#3111);
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  border-bottom-left-radius:5px;
  -webkit-box-shadow: inset 0px 1px 0px #2ab7ec, 0px 5px 0px 0px #497a78, 0px 10px 5px #999;
          box-shadow: inset 0px 1px 0px #2ab7ec, 0px 5px 0px 0px #497a78, 0px 10px 5px #999;
}

.shadow {
  background: #000;
  border-radius: 12px 12px 4px 4px;
  -webkit-box-shadow: 0 0 20px 10px #000;
          box-shadow: 0 0 20px 10px #000;
  height: 12px;
  margin: 30px auto;
  opacity: 0.2;
  width: 270px;
}


input[type="submit"]:active {
  top:3px;
  -webkit-box-shadow: inset 0px 1px 0px #2ab7ec, 0px 2px 0px 0px #31524d, 0px 5px 3px #999;
          box-shadow: inset 0px 1px 0px #2ab7ec, 0px 2px 0px 0px #31524d, 0px 5px 3px #999;
}


input:focus,
select:focus,
textarea:focus,
button:focus {
    outline: none;
}



